*{ margin:0; padding:0;}
html{ width:100%; height:100%; overflow:hidden;}
body{ width:100%; height:100%; font: 14px/1.5 Tahoma,Arial,Roboto,"Droid Sans","Helvetica Neue","Droid Sans Fallback","Heiti SC",sans-self;}
li{ list-style:none;}
#root , #main{ width:100%; height:100%; position:relative; margin:0 auto;}

#musicHeader{ width:100%; height: 50px; background:black;z-index: 10;overflow: hidden; position: absolute; left: 0; top: 0; color:white; text-align: center; line-height: 50px; font-size: 18px;}
#musicHeader span,#musicHeader a{ position: absolute; left: 10px;text-decoration: none;color: inherit;overflow: hidden; }
#musicList{ width: 100%; position: absolute; top:50px; bottom:65px; background:url(/images/bg.jpg); background-size: cover; color:white; overflow-y: auto; }
#musicList li{position: relative; z-index: 10 ; height:60px; border-bottom: 1px #999 solid; background:transparent; opacity: 0.9; }
#musicList li.active{ border-left:4px lightblue solid; background: rgba(0,0,0,0.4);}
#musicList .listOrder{ width:60px; float: left; font-size: 20px; line-height: 60px; text-align: center; }
#musicList .listName{ float: left; }
#musicList .listName h3{width: 200px;margin-top: 10px; overflow: hidden; white-space:nowrap; text-overflow: ellipsis}
#musicList .listName p{overflow: hidden;white-space: nowrap;text-overflow: ellipsis}
.loading {
  position: fixed;
  z-index: 999;
  height: 2em;
  width: 2em;
  overflow: show;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

/* Transparent Overlay */
.loading:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
}

/* :not(:required) hides these rules from IE9 and below */
.loading:not(:required) {
  /* hide "loading..." text */
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

.loading:not(:required):after {
  content: '';
  display: block;
  font-size: 10px;
  width: 1em;
  height: 1em;
  margin-top: -0.5em;
  -webkit-animation: spinner 1500ms infinite linear;
  animation: spinner 1500ms infinite linear;
  border-radius: 0.5em;
  box-shadow: rgba(0, 0, 0, 0.75) 1.5em 0 0 0, rgba(0, 0, 0, 0.75) 1.1em 1.1em 0 0, rgba(0, 0, 0, 0.75) 0 1.5em 0 0, rgba(0, 0, 0, 0.75) -1.1em 1.1em 0 0, rgba(0, 0, 0, 0.75) -1.5em 0 0 0, rgba(0, 0, 0, 0.75) -1.1em -1.1em 0 0, rgba(0, 0, 0, 0.75) 0 -1.5em 0 0, rgba(0, 0, 0, 0.75) 1.1em -1.1em 0 0;
}

/* Animation */

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
#musicAudio{ width:100%; height: 65px; background:url(/images/list_audioBg.png); position: absolute;z-index: 100; left:0; bottom: 0; display: flex; }
	#musicAudio .audioPlay{ width:40px;  height:40px; background:url(/images/list_audioPlay.png); background-size:cover; margin:10px 20px;}
	#musicAudio .audioProgress{ height:2px; background:white; position: relative; flex:1 1; top:31px; margin-right: 20px;}
	#musicAudio .audioBar{ width:15px; height:15px; background:#29abe2; border-radius:50%; position:absolute;z-index: 10; left:-7px; top:-7px;  }
	#musicAudio .audioNow{ width:0; height:2px; background:#29abe2; position:absolute; left:0; top:0; }
#pic{ width: 100%; position: absolute; top:50px; bottom:65px; background:url(/images/bg.jpg); background-size: cover; color:white; overflow-y: auto; display: flex;}
#pic div{ position: relative;z-index: 10; width: 250px;height: 250px;border-radius: 50%;overflow: hidden;margin: auto;box-shadow: 1px 1px 80px black}
#pic div img{width: 100%;height: 100%}
#pic .picRoll{-webkit-animation:8s linear RollPic infinite;animation:8s linear RollPic infinite;-webkit-animation-play-state: paused;animation-play-state: paused;}
@-webkit-keyframes RollPic{
    0%{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
    100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}
}
@keyframes RollPic{
    0%{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
    100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}
}

html, body {
	background: black;
}
#musicLyric{ width: 100%; position: absolute; top:50px; bottom:65px; background:url(/images/detailsBg.jpg); background-size: cover; color:white; overflow-y: hidden; }
#musicLyric ul{ width:100%; text-align:center; color:white; font-size:14px; position:absolute;margin-top: 55%; top:0; left:0; transition:.2s; -webkit-transition:.2s;} 
#musicLyric li{ margin-top:15px; position: relative;z-index: 10;}
#musicLyric li.active{ color:#00ffff;}
