#musicList{ width: 100%; position: absolute; top:50px; bottom:65px; background:url(/images/bg.jpg); background-size: cover; color:white; overflow-y: auto; }
#musicList li{position: relative; z-index: 10 ; height:60px; border-bottom: 1px #999 solid; background:transparent; opacity: 0.9; }
#musicList li.active{ border-left:4px lightblue solid; background: rgba(0,0,0,0.4);}
#musicList .listOrder{ width:60px; float: left; font-size: 20px; line-height: 60px; text-align: center; }
#musicList .listName{ float: left; }
#musicList .listName h3{width: 200px;margin-top: 10px; overflow: hidden; white-space:nowrap; text-overflow: ellipsis}
#musicList .listName p{overflow: hidden;white-space: nowrap;text-overflow: ellipsis}